$top-nav-height: 60px;
$standard-padding: 20px;

@function standard-border($color) {
	@return 1px solid colors($color);
}

@function standard-shadow($color) {
	@return 0px 0px 7px -1px colors($color);
}

@function standard-hover-shadow($color) {
	@return 0px 0px 15px -1px colors($color);
}



